import React from 'react';

const myinfo = () => {
    const currentTime = new Date().toLocaleString();
    return (
        <div className="nav-texts">
            <section>
            <h1>About Me</h1>
            <p>........</p>
            <p>{currentTime}</p>
            </section>
        </div>
    );
};

export default myinfo;