import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { FiSearch } from 'react-icons/fi';
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import './navbar.css';

const Navbar = ({ searchQuery, setSearchQuery }) => {
  const [showSearch, setShowSearch] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false); // State for hamburger menu
  const location = useLocation();

  const links = [
    { name: 'About', path: '/about' },
    { name: 'Research', path: '/research' },
    { name: 'Team', path: '/team' },
    { name: 'Courses', path: '/courses' },
    { name: 'Service', path: '/service' },
    { name: 'Equipment: XR-DRIVE', path: '/xrdrive' },
    { name: 'Openings & News', path: '/news' },
//    { name: 'Join Our Research', path: '/join-research' },
    { name: 'Contact Us', email: 'liru@montclair.edu' },
  ];

  const getActiveLink = () => {
    const path = location.pathname;
    if (path === '/' || path === '/about') return 'About';
    const matchingLink = links.find((link) => link.path === path);
    return matchingLink ? matchingLink.name : '';
  };

  const [activeLink, setActiveLink] = useState(getActiveLink());

  useEffect(() => {
    setActiveLink(getActiveLink());
  }, [location]);

  const toggleSearch = () => setShowSearch((prev) => !prev);

  const toggleMenu = () => setMenuOpen((prev) => !prev);

  return (
    <nav className="navbar">
      <div className="logo">MIAC Lab</div>

      {/* Hamburger Icon */}
      <div className="hamburger-icon" onClick={toggleMenu}>
        {menuOpen ? <AiOutlineClose size={18} /> : <AiOutlineMenu size={18} />}
      </div>

      <ul className={`nav-links ${menuOpen ? 'open' : ''}`}>
        {links.map((link) => (
          <li key={link.name}>
            {link.path ? (
              <Link
                to={link.path}
                className={`nav-button ${activeLink === link.name ? 'active' : ''}`}
                onClick={() => setActiveLink(link.name)}
              >
                {link.name}
              </Link>
            ) : link.email ? (
              <a
                href={`mailto:${link.email}`}
                className={`nav-button contact-btn ${activeLink === link.name ? 'active' : ''}`}
              >
                {link.name}
              </a>
            ) : null}
          </li>
        ))}
      </ul>

      <div className="search-icon" onClick={toggleSearch}>
        <FiSearch size={20} />
      </div>
      {showSearch && (
        <input
          type="text"
          className="search-bar"
          placeholder="Search..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          autoFocus
        />
      )}
    </nav>
  );
};

export default Navbar;
